import { NavigationScreens } from '../../../utils/constants';

interface NavigationTarget {
  name: `${NavigationScreens}`;
  properties?: Record<string, unknown>;
}

export class Navigation {
  public target: NavigationTarget;
  constructor(
    public screenName: `${NavigationScreens}`,
    properties?: Record<string, unknown> | Record<string, never>,
  ) {
    this.target = {
      name: screenName,
      properties,
    };
  }

  // ToDo: refactor navigation target JSON type (JSON String -> JSON Object)
  getNavigationTargetJSON(): string {
    return JSON.stringify(this.target);
  }

  toJSON(): Record<string, unknown> {
    return {
      target: this.target,
    };
  }
}

class ApplicationWindow {
  navigate(target: string): boolean {
    try {
      JSON.parse(target);
    } catch (e) {
      console.log(e);
      return false;
    }
    const {
      target: { name },
    } = JSON.parse(target);

    console.log(`Navigate to ${name} screen`);
    return true;
  }
}

const applicationWindowInstance = new ApplicationWindow();

export class Application {
  constructor(public appVersion: string = '1.0.0.0') {}

  getApplicationWindow(): ApplicationWindow {
    return applicationWindowInstance;
  }

  getVersion(): string {
    return this.appVersion;
  }
}

export const applicationInstance = new Application();
