import { NotificationNames, NOTIFICATIONS } from '../../../utils/constants';

const SCREEN_DETAILS: ScreenDetails = {
  style: 'trayNotification',
  name: NotificationNames.checkNewDriver,
  parameters: {
    updatesFound: '10',
    deviceInfo: {
      name: 'Intel(R) 100 series Chipset Family',
      type: 'TN',
      provider: 'Intel(R)',
      installed: {
        version: '2.0.0.456',
        releaseDate: '09/12/2020',
      },
      available: {
        version: '4.0.0.456',
        releaseDate: '09/12/2020',
      },
    },
  },
};

interface DriverInfo {
  version: string;
  releaseDate: string;
}

export interface DeviceInfo {
  name: string;
  type: string;
  provider: string;
  installed?: DriverInfo;
  available?: DriverInfo;
}

export interface ScreenDetails {
  style: string;
  name: typeof NOTIFICATIONS[number]['name'];
  parameters?: {
    updatesFound?: string;
    deviceInfo?: DeviceInfo;
  };
}

export class Screen {
  private screenDetails: ScreenDetails;
  private screenDetailsJSON;
  constructor(
    public name: string = SCREEN_DETAILS.name,
    public parameters = SCREEN_DETAILS.parameters,
    public screenVersion: string = '1.0.0.0',
  ) {
    this.screenDetails = {
      style: SCREEN_DETAILS.style,
      name: this.name as typeof SCREEN_DETAILS['name'],
      parameters: this.parameters,
    };

    this.screenDetailsJSON = JSON.stringify(this.screenDetails);
  }

  getDetails(): string {
    return this.screenDetailsJSON;
  }

  getVersion(): string {
    return this.screenVersion;
  }
}

export const screenInstance = new Screen();
