import React from 'react';
import styled from 'styled-components';
import { ThemeColors } from '../../styled';

interface StyledIconButtonProps {
  color?: `${ThemeColors}`;
  size?: string;
}

const StyledIconButton = styled.button<StyledIconButtonProps>(
  ({ theme, color }) => `
    position: relative;
    display: inline-block;
    width: 2rem;
    height: 2rem;
    line-height: 0.625;
    vertical-align: middle;
    border: 0;
    border-radius: 50%;
    outline: none;
    background-color: ${color ? theme.colors[color] : 'transparent'};
    cursor: pointer;

    > img {
      display: inline-block;
      width: 1rem;
      height: auto;
      margin: auto;
    }

    &:before {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      border-radius: 50%;
      background-color: transparent;
      transition: background-color 0.2s ease-in-out;
    }

    &:hover {
      &:before {
        background-color: rgba(255, 255, 255, 0.1);
      }
    }

    &:active {
      &:before {
        background-color: rgba(255, 255, 255, 0.2);
      }
    }
  `,
);

interface IconButtonProps extends StyledIconButtonProps {
  icon: string;
  className?: string;
  onClick?: () => void;
}

export const IconButton = ({
  icon,
  color,
  size,
  className,
  onClick,
}: IconButtonProps): React.ReactElement => {
  return (
    <StyledIconButton
      className={className}
      color={color}
      size={size}
      onClick={onClick}
    >
      {!!icon && <img src={icon} />}
    </StyledIconButton>
  );
};
