import { api } from '.';
import {
  Notification,
  NOTIFICATIONS,
  NavigationScreens,
  Product,
  PRODUCTS,
  NotificationNames,
} from '../../utils/constants';
import { DeviceInfo, ScreenDetails } from './modules/screen';

// TODO: replace png to svg icons
import DRLogo from '../../assets/images/dr/logo.png';
import DULogo from '../../assets/images/du/logo.png';
import SDULogo from '../../assets/images/sdu/logo.png';
import { Navigation } from './modules/application';

export const getProductLogo = (productCode: Product['code']): string => {
  switch (productCode.toLowerCase()) {
    case 'dr':
      return DRLogo;
    case 'du':
      return DULogo;
    case 'sdu':
      return SDULogo;
    default:
      return '';
  }
};

export const getProductName = (productCode: Product['code']): Product['name'] =>
  PRODUCTS.filter(
    (product) => product.code.toLowerCase() === productCode.toLowerCase(),
  )[0].name;

export const getProductCode = (): Product['code'] =>
  api.applicationInfo.getProductCode();

export const getParsedDetails = (): ScreenDetails => {
  return JSON.parse(api.screen.getDetails());
};

export const getUpdates = (): number => {
  const parsedDetails = getParsedDetails();
  const parsedUpdates = parsedDetails.parameters?.updatesFound
    ? parseInt(parsedDetails.parameters.updatesFound, 10)
    : 0;

  if (parsedUpdates < 0) return 0;
  return parsedUpdates;
};

export const getDeviceName = (): string => api.systemInfo.getManufacturer();

export const getNotificationDataByName = (
  name: typeof NOTIFICATIONS[number]['name'],
): Notification =>
  NOTIFICATIONS.filter((notification) => notification.name === name)[0];

export const getNotificationData = (): Notification => {
  const parsedDetails = getParsedDetails(),
    updates = getUpdates();

  const isParametersEmpty =
    parsedDetails.parameters &&
    Object.keys(parsedDetails.parameters).length === 0 &&
    parsedDetails.parameters.constructor === Object;

  switch (parsedDetails.name) {
    case NotificationNames.scanResults:
      if (isParametersEmpty || (!isParametersEmpty && updates === 0)) {
        return getNotificationDataByName('scanCompleted');
      } else {
        return getNotificationDataByName('scanResults');
      }
    case NotificationNames.connectedDevice:
      if (parsedDetails.parameters?.deviceInfo?.available) {
        return getNotificationDataByName('connectedDeviceUpdate');
      } else {
        return getNotificationDataByName('connectedDeviceUpToDate');
      }
    default:
      return getNotificationDataByName(parsedDetails.name);
  }
};

export const navigateToScreen = (
  targetScreenName: `${NavigationScreens}`,
): void => {
  const appWindow = api.application.getApplicationWindow();

  const navigationTarget = JSON.stringify(new Navigation(targetScreenName));

  appWindow.navigate(navigationTarget);
};

export const hideWindow = (): void => {
  api.basewnd.hide();
};

export const closeWindow = (): void => {
  api.basewnd.close();
};

export const getGAProfileId = (): Product['gaProfileId'] => {
  const productCode = getProductCode().toLowerCase();

  return PRODUCTS.filter(
    (product) => product.code.toLowerCase() === productCode,
  )[0].gaProfileId;
};

export const getGAClientId = (): string => {
  return api.applicationInfo.getTrackingId();
};

export const getAppLanguage = (): string => {
  return api.applicationInfo.getLanguageCode();
};

export const getAppName = (): Product['appName'] => {
  const productCode = getProductCode().toLowerCase();

  return PRODUCTS.filter(
    (product) => product.code.toLowerCase() === productCode,
  )[0].appName;
};

export const getAppVersion = (): string => {
  return api.applicationInfo.getApplicationVersion();
};

export const getDeviceInfo = (): DeviceInfo | undefined => {
  const parsedDetails = getParsedDetails();

  return parsedDetails.parameters?.deviceInfo;
};
