export class Basewnd {
  constructor() {}

  hide(): void {
    // @ts-ignore
    return chrome.webview.hostObjects.sync.basewnd.hide();
    // console.log('basewnd hide method called');
  }

  close(): void {
    // @ts-ignore
    return chrome.webview.hostObjects.sync.basewnd.close();
    // console.log('basewnd close method called');
  }

  closeWithExitCode(exitCode: number): void {
    // @ts-ignore
    return chrome.webview.hostObjects.sync.basewnd.closeWithExitCode(exitCode);
    // console.log(`basewnd closeWithExitCode method with exitCode: ${exitCode} called`,);
  }

  show(width: number, height: number): void {
    // @ts-ignore
    return chrome.webview.hostObjects.sync.basewnd.show(width,height);
    // console.log(`basewnd show method with width: ${width} and height: ${height} called`,);
  }

  getDPI(): number {
    // @ts-ignore
    return chrome.webview.hostObjects.sync.basewnd.getDPI;
    // return this.DPI;
  }

  minimize(): void {
    // @ts-ignore
    return chrome.webview.hostObjects.sync.basewnd.minimize();
    // console.log('basewnd minimize method called');
  }

  isMinimizeSupported(): boolean {
    // @ts-ignore
    return chrome.webview.hostObjects.sync.basewnd.isMinimizeSupported;
    // return this.isMinimizeSupport;
  }
}

export const basewndInstance = new Basewnd();
