export type Product = typeof PRODUCTS[number];
export type ProfileID = typeof PRODUCTS[number]['gaProfileId'];

export enum NotificationTypes {
  updateAvailable = 'updateAvailable', // Red color notifications
  updateSucceed = 'updateSucceed', // Green color notifications
  updateInfo = 'updateInfo', // Yellow color notifications
}

export enum NotificationNames {
  scanResults = 'scanResults',
  scanCompleted = 'scanCompleted',
  timeToScan = 'timeToScan',
  scanCancelled = 'scanCancelled',
  connectedDevice = 'connectedDevice', // For both connectedDeviceUpdate & connectedDeviceUpToDate
  connectedDeviceUpdate = 'connectedDeviceUpdate',
  connectedDeviceUpToDate = 'connectedDeviceUpToDate',
  checkNewDriver = 'checkNewDriver',
}

export enum NavigationScreens {
  options = 'options',
  PCScan = 'PCScan',
  scheduleOptions = 'scheduleOptions',
}

export enum GAScreenNames {
  UpdateDriversTrayPopup = 'UpdateDriversTrayPopup',
  UpToDateDriversTrayPopup = 'UpToDateDriversTrayPopup',
  CompleteScanTrayPopup = 'CompleteScanTrayPopup',
  ConnectedDeviceTrayPopup = 'ConnectedDeviceTrayPopup',
  CheckNewDriverTrayPopup = 'CheckNewDriverTrayPopup',
}

export enum GAActionNames {
  UpdateNow = 'UpdateNow',
  ScheduleScan = 'ScheduleScan',
  DriversUpToDate = 'DriversUpToDate', // replace after timeToScan Notification implementation
  ScanNow = 'ScanNow',
  Settings = 'Settings',
  Close = 'Close',
}

export type NotificationTranslateKey = TFuncKey<'translations'> | '';

export type VerticalAlign = 'top' | 'middle';

export interface Notification {
  type: `${NotificationTypes}`;
  name: `${NotificationNames}`;
  bodyVerticalAlign: VerticalAlign;
  bodyIcon: string;
  bodyTitle: NotificationTranslateKey;
  bodySubtitle: NotificationTranslateKey;
  hasBodyInfo: boolean;
  footerMessage: NotificationTranslateKey;
  footerBtnText: NotificationTranslateKey;
  footerBtnClickNavScreen: `${NavigationScreens}`;
  footerBtnGAActionName: `${GAActionNames}`;
  GAScreenName: `${GAScreenNames}`;
}

