import React, { useContext, useEffect, useRef } from 'react';
import { FallbackProps } from 'react-error-boundary';

import styled from 'styled-components';
import { APIContext } from '../../services/api';
import {
  getGAProfileId,
  getProductCode,
  getUpdates,
} from '../../services/api/actions';

export const StyledErrorNotification = styled.div`
  width: 1000px;
  height: 1000px;
`;

export const ErrorTrayNotification = ({
  error: { message },
}: FallbackProps): React.ReactElement => {
  const errorNotificationRef = useRef<HTMLDivElement>(null);
  const api = useContext(APIContext);

  useEffect(() => {
    if (process.env.REACT_APP_ENV === 'test') {
      const width = errorNotificationRef.current?.offsetWidth as number,
        height = errorNotificationRef.current?.offsetHeight as number;
      api.basewnd.show(width, height);
    } else {
      api.basewnd.close();
    }
  }, [api.basewnd]);

  console.log(`error message`, message);

  const states = [
    { name: 'ProductCode', data: getProductCode() },
    { name: 'GAProfileId', data: getGAProfileId() },
    { name: 'Updates', data: getUpdates() },
    {
      name: 'ScreenDetails',
      data: api.screen.getDetails(),
    },
    {
      name: 'DeviceName',
      data: api.systemInfo.getManufacturer(),
    },
    {
      name: 'TrackingId',
      data: api.applicationInfo.getTrackingId(),
    },
    {
      name: 'LanguageCode',
      data: api.applicationInfo.getLanguageCode(),
    },
  ];

  return (
    <div ref={errorNotificationRef}>
      <StyledErrorNotification>
        <h1>Error</h1>
        <h2>REACT_APP_ENV: {process.env.REACT_APP_ENV}</h2>
        <p>{JSON.stringify(message)}</p>
        <h2>API states</h2>
        <ul>
          {states.map((state, i) => (
            <li key={i}>
              <>
                {state.name}
                <div>{JSON.stringify(state.data)}</div>
              </>
            </li>
          ))}
        </ul>
      </StyledErrorNotification>
    </div>
  );
};
