import React from 'react';
import styled from 'styled-components';
import { Product } from '../../utils/constants';
import { IconButton } from '../Buttons/';

import optionsIcon from '../../assets/images/common/settings.svg';
import closeIcon from '../../assets/images/common/close.svg';

interface StyledHeaderProps {
  color: string;
}

interface HeaderProps extends StyledHeaderProps {
  productLogo: string;
  productName: Product['name'];
  onCloseClick: () => void;
  onOptionsClick: () => void;
}

const StyledHeader = styled.div<StyledHeaderProps>`
  display: table;
  width: 100%;
  height: 3rem;
  background-color: ${({ color }) => color};

  .product-info {
    display: table-cell;
    padding-left: 1rem;
    vertical-align: middle;
    white-space: nowrap;
  }

  .product-logo {
    display: inline-block;
    width: 1.6875rem;
    height: 1.6875rem;
    margin-right: 0.75rem;
    vertical-align: middle;
  }

  .product-name {
    display: inline-block;
    color: #fff;
    font-size: 1rem;
    font-weight: 700;
    line-height: 1;
    text-shadow: 0 0 1px rgba(0, 0, 0, 0.66);
    vertical-align: middle;
  }

  .actions {
    display: table-cell;
    padding-right: 0.5rem;
    text-align: right;
    vertical-align: middle;
    white-space: nowrap;
  }

  .close-btn {
    > img {
      width: 0.9375rem;
      line-height: 0.9375;
    }
  }
`;

export const Header = ({
  color,
  productLogo,
  productName,
  onCloseClick,
  onOptionsClick,
}: HeaderProps): React.ReactElement => {
  return (
    <StyledHeader color={color}>
      <div className="product-info">
        <img className="product-logo" src={productLogo} />
        <span className="product-name">{productName}</span>
      </div>
      <div className="actions">
        <IconButton icon={optionsIcon} onClick={onOptionsClick}></IconButton>
        <IconButton
          className="close-btn"
          icon={closeIcon}
          onClick={onCloseClick}
        ></IconButton>
      </div>
    </StyledHeader>
  );
};
