import { useContext } from 'react';
import { ThemeContext } from 'styled-components';
import { NotificationTypes } from '../utils/constants';

export const useHeaderBgColorByNotificationType = (
  type: `${NotificationTypes}`,
): string => {
  const theme = useContext(ThemeContext);

  switch (type) {
    case NotificationTypes.updateSucceed:
      return theme.colors.green;
    case NotificationTypes.updateAvailable:
      return theme.colors.red;
    case NotificationTypes.updateInfo:
      return theme.colors.yellow;
    default:
      return theme.colors.primary;
  }
};

export const useIconBgColorByNotificationType = (
  type: `${NotificationTypes}`,
): string => {
  const theme = useContext(ThemeContext);

  switch (type) {
    case NotificationTypes.updateSucceed:
      return theme.colors.lightGreen;
    case NotificationTypes.updateAvailable:
      return theme.colors.lightRed;
    case NotificationTypes.updateInfo:
      return theme.colors.lightYellow;
    default:
      return theme.colors.lightGrey;
  }
};
