import ReactGA from 'react-ga';
import { GAActionNames, GAScreenNames } from '../../utils/constants';
import {
  getAppLanguage,
  getAppName,
  getAppVersion,
  getGAClientId,
  getGAProfileId,
} from '../api/actions';

interface UseAnalytics {
  initGA: () => void;
  sendScreenView: (screenName: `${GAScreenNames}`) => void;
  sendAction: (
    eventAction: `${GAActionNames}`,
    screenName: `${GAScreenNames}`,
    callback?: (() => void) | undefined,
  ) => void;
}

export const useAnalytics = (): UseAnalytics => {
  const TRACKING_ID = getGAProfileId(),
    CLIENT_ID = getGAClientId(),
    APP_LANGUAGE = getAppLanguage(),
    APP_NAME = getAppName(),
    APP_VERSION = getAppVersion();

  const initGA = () => {
    // Enable debug mode on the local development environment
    const isDev = process.env.NODE_ENV === 'development',
      isTestMode =
        CLIENT_ID === '' || // TODO: get unique clientId for testing
        process.env.REACT_APP_ENV === 'test' ||
        process.env.NODE_ENV !== 'production';

    ReactGA.initialize(TRACKING_ID, {
      debug: isDev,
      testMode: isTestMode,
      gaOptions: {
        cookieDomain: 'auto',
        storage: 'none',
        clientId: CLIENT_ID,
        cookieFlags: 'SameSite=None; Secure',
      },
    });

    ReactGA.set({
      checkProtocolTask: null,
      language: APP_LANGUAGE,
    });
  };

  const sendScreenView = (screenName: `${GAScreenNames}`) => {
    ReactGA.ga('send', 'screenview', {
      appName: APP_NAME,
      screenName,
      appVersion: APP_VERSION,
    });
  };

  const sendAction = (
    eventAction: `${GAActionNames}`,
    screenName: `${GAScreenNames}`,
    callback?: () => void,
  ) => {
    ReactGA.ga('send', {
      hitType: 'event', // TODO: get from attributes || constant || API
      eventCategory: 'TrayPopup', // TODO: get from attributes || constant || API
      eventAction,
      screenName,
      appName: getAppName(),
      appVersion: getAppVersion(),
    });

    if (typeof callback === 'function') callback();
  };

  return { initGA, sendScreenView, sendAction } as const;
};
