import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';

// import translationDA from './locales/da.json';
// import translationDE from './locales/de.json';
import translationEN from './locales/en.json';
// import translationES from './locales/es.json';
// import translationFI from './locales/fi.json';
// import translationFR from './locales/fr.json';
// import translationIT from './locales/it.json';
// import translationJA from './locales/ja.json';
// import translationNL from './locales/nl.json';
// import translationNO from './locales/no.json';
// import translationPT_BR from './locales/pt-br.json';
// import translationRU from './locales/ru.json';
// import translationSV from './locales/sv.json';
// import translationTR from './locales/tr.json';
// import translationZH_TW from './locales/zh-tw.json';

import { getAppLanguage } from './services/api/actions';

export const resources = {
  // da: {
  //   translations: translationDA,
  // },
  // de: {
  //   translations: translationDE,
  // },
  en: {
    translations: translationEN,
  },
  // es: {
  //   translations: translationES,
  // },
  // fi: {
  //   translations: translationFI,
  // },
  // fr: {
  //   translations: translationFR,
  // },
  // it: {
  //   translations: translationIT,
  // },
  // ja: {
  //   translations: translationJA,
  // },
  // nl: {
  //   translations: translationNL,
  // },
  // no: {
  //   translations: translationNO,
  // },
  // 'pt-BR': {
  //   translations: translationPT_BR,
  // },
  // ru: {
  //   translations: translationRU,
  // },
  // sv: {
  //   translations: translationSV,
  // },
  // tr: {
  //   translations: translationTR,
  // },
  // 'zh-TW': {
  //   translations: translationZH_TW,
  // },
} as const;

const appLanguage = getAppLanguage();

i18n
  // TODO: Implement language detection
  // .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    // we init with resources
    resources,
    fallbackLng: 'en',
    debug: true,

    // have a common namespace used around the full app
    ns: ['translations'],
    defaultNS: 'translations',

    keySeparator: false, // we use content as keys

    interpolation: {
      formatSeparator: ',',
    },

    react: {
      wait: true,
    },
  });

i18n.changeLanguage(appLanguage);

export default i18n;
