import React, { useEffect, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { DeviceInfo } from '../../services/api/modules/screen';
import cx from 'classnames';
import styled from 'styled-components';

import { truncate } from '../../styles/mixins';

interface BodyInfoProps extends DeviceInfo {}

const StyledBodyInfo = styled.div`
  .device-name {
    margin-top: 1.5rem;
    font-weight: 700;
    white-space: nowrap;

    &.is-truncated {
      ${truncate}
    }
  }

  .device-info {
    margin: 0;
    padding: 0.25rem 0 0;
    list-style-type: none;
    border-top: ${({ theme }) => `1px solid ${theme.dividerColor}`};

    &__item {
      margin-top: 0.25rem;
      line-height: 1.375;

      > b {
        margin-left: 0.4375rem;
        font-weight: 700;
      }
    }
  }
`;

export const BodyInfo = ({
  name,
  installed,
  available,
}: BodyInfoProps): React.ReactElement => {
  const { t } = useTranslation();

  const deviceNameRef = useRef<HTMLParagraphElement>(null);
  const [titleTruncated, setTitleTruncated] = useState(false);

  const { version, releaseDate } = available || installed || {};

  useEffect(() => {
    // Truncate device name if string is too long
    if (deviceNameRef.current) {
      const hasTruncateTitle =
        deviceNameRef.current.scrollWidth > deviceNameRef.current.offsetWidth;
      setTitleTruncated(hasTruncateTitle);
    }
  }, []);

  return (
    <StyledBodyInfo>
      <p
        ref={deviceNameRef}
        className={cx('device-name', {
          ['is-truncated']: titleTruncated,
        })}
        title={titleTruncated ? name : undefined}
      >
        {name}
      </p>
      <ul className="device-info">
        <li className="device-info__item">
          {available ? t('VersionAvailable') : t('CurrentVersion')}
          <b>{version}</b>
        </li>
        <li className="device-info__item">
          {t('ReleasedOn')}
          <b>{releaseDate}</b>
        </li>
      </ul>
    </StyledBodyInfo>
  );
};
