import React, { useContext, useEffect, useRef } from 'react';
import { APIContext } from '../../services/api';

import { TrayNotification } from '../../components/TrayNotification';
import {
  getAppLanguage,
  getNotificationData,
  // getNotificationDataByName,
} from '../../services/api/actions';
import { useAnalytics } from '../../services/analytics';
import i18n from '../../i18n';

// const ErrorComponent = () => {
//   throw Error('Caboom !!!');
// };

const App: React.FC = () => {
  const notificationRef = useRef<HTMLDivElement>(null);
  const api = useContext(APIContext);
  const { initGA } = useAnalytics();
  const notificationData = getNotificationData();
  const appLanguage = getAppLanguage();

  // GoogleAnalytics initialization
  initGA();

  // Init app with defined language
  i18n.changeLanguage(appLanguage);

  // Show tray notification after mount & calculating dimensions
  useEffect(() => {
    const width = notificationRef.current?.offsetWidth as number,
      height = notificationRef.current?.offsetHeight as number;
    api.basewnd.show(width, height);
  }, [api.basewnd]);

  // For dev & test purposes
  // const notificationDataByName = getNotificationDataByName('scanCancelled');
  console.log(`api`, api);

  return (
    <>
      {/* <ErrorComponent></ErrorComponent> */}
      <div ref={notificationRef}>
        <TrayNotification {...notificationData}></TrayNotification>
      </div>
    </>
  );
};

export default App;
