import React from 'react';
import { Application, applicationInstance } from './modules/application';
import {
  ApplicationInfo,
  applicationInfoInstance,
} from './modules/applicationInfo';
import { Basewnd, basewndInstance } from './modules/basewnd';
import { Screen, screenInstance } from './modules/screen';
import { SystemInfo, systemInfoInstance } from './modules/systemInfo';

declare global {
  interface window {
    external: Api;
  }
}

interface Api {
  application: Application;
  applicationInfo: ApplicationInfo;
  screen: Screen;
  systemInfo: SystemInfo;
  basewnd: Basewnd;
}

class API implements Api {
  get application(): Application {
    return applicationInstance;
  }
  get applicationInfo(): ApplicationInfo {
    return applicationInfoInstance;
  }
  get screen(): Screen {
    return screenInstance;
  }
  get systemInfo(): SystemInfo {
    return systemInfoInstance;
  }
  get basewnd(): Basewnd {
    return basewndInstance;
  }
}

const API_ENDPOINT = <Api>(<unknown>window.external);

export const api: Api =
  process.env.NODE_ENV === 'production' ? API_ENDPOINT : new API();

export const APIContext = React.createContext(api);
