import 'react-app-polyfill/ie9';
import 'react-app-polyfill/stable';

import React from 'react';
import ReactDOM from 'react-dom';
import { ErrorBoundary } from 'react-error-boundary';
import { createGlobalStyle, ThemeProvider } from 'styled-components';
import { normalize } from 'polished';
import { APIContext, api } from './services/api';
import { defaultTheme } from './styles/defaultTheme';
import reportWebVitals from './reportWebVitals';

import App from './views/App';
import { ErrorTrayNotification } from './components/TrayNotification/ErrorTrayNotification';

import './i18n';

const GlobalStyle = createGlobalStyle`
  ${normalize()}

  html {
    font-size: ${({ theme }) => theme.remFontSize};
  }

  body {
    color: ${({ theme }) => theme.textColor};
    font-family: 'Segoe UI', 'Open Sans', 'Calibri', 'Arial', 'Helvetica', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1.33;

    // #root styles 
    .root {
      display: inline-block;
      overflow: hidden;
    }
  }
`;

ReactDOM.render(
  <React.StrictMode>
    <ThemeProvider theme={defaultTheme}>
      <GlobalStyle />
      <ErrorBoundary FallbackComponent={ErrorTrayNotification}>
        <APIContext.Provider value={api}>
          <App />
        </APIContext.Provider>
      </ErrorBoundary>
    </ThemeProvider>
  </React.StrictMode>,
  document.getElementById('root'),
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
