import React from 'react';
import styled from 'styled-components';
import { DeviceInfo } from '../../services/api/modules/screen';
import { VerticalAlign } from '../../utils/constants';
import { BodyInfo } from '../BodyInfo';

interface StyledBodyProps {
  color: string;
  verticalAlign: VerticalAlign;
}

interface BodyProps extends StyledBodyProps {
  bodyIcon: string;
  bodyTitle?: string;
  bodySubtitle?: string;
  bodyInfo?: DeviceInfo;
}

const StyledBody = styled.div<StyledBodyProps>(
  ({ theme: { colors }, color, verticalAlign }) => `
    display: table;
    width: 100%;
    min-height: 5.625rem;
    table-layout: fixed;

    .body-icon {
      display: table-cell;
      width: 2.5rem;
      padding: 1rem 0 1rem 1rem;
      vertical-align: ${verticalAlign};

      &__wrapper {
        display: inline-block;
        position: relative;
        width: 2.5rem;
        height: 2.5rem;
        line-height: 2;
        text-align: center;

        > img {
          display: inline-block;
          max-width: 100%;
          height: auto;
          line-height: normal;
          vertical-align: middle;
        }

        &::after {
          content: '';
          position: absolute;
          z-index: -1;
          top: 0;
          bottom: 0;
          left: ${
            color === colors.lightRed || color === colors.lightGreen
              ? '0.4375rem'
              : '0'
          };
          width: 101%;
          height: 101%;
          margin: auto;
          border-radius: 50%;
          background-color: ${color};
        }
      }
    }

    .body-message {
      display: table-cell;
      width: 100%;
      padding: 1rem 1rem 1rem 1.25rem;
      vertical-align: ${verticalAlign};
    }

    .body-title {
      margin: 0 0 0.25rem;
    }

    .body-subtitle {
      margin: 0;
      /* max-width: 13.4375rem; */
    }
  `,
);

export const Body = ({
  color,
  verticalAlign,
  bodyIcon,
  bodyTitle,
  bodySubtitle,
  bodyInfo,
}: BodyProps): React.ReactElement => {
  return (
    <StyledBody color={color} verticalAlign={verticalAlign}>
      <div className="body-icon">
        <div className="body-icon__wrapper">
          <img src={bodyIcon} />
        </div>
      </div>

      <div className="body-message">
        {bodyTitle && <h4 className="body-title">{bodyTitle}</h4>}
        {bodySubtitle && <p className="body-subtitle">{bodySubtitle}</p>}
        {bodyInfo && <BodyInfo {...bodyInfo}></BodyInfo>}
      </div>
    </StyledBody>
  );
};
