import { Product, PRODUCTS } from '../../../utils/constants';

export class ApplicationInfo {
  constructor(
    public language: string = 'en',
    public product = PRODUCTS[0],
    public buildId: string = '',
    public trackingId: string = '',
  ) {}

  getProductCode(): Product['code'] {
    return this.product.code;
  }

  getApplicationVersion(): string {
    return this.product.appVersion;
  }

  getMinutesSinceInstall(): number {
    return Math.round(new Date().getTime() / (1000 * 60));
  }

  getDaysSinceInstall(): number {
    return Math.round(new Date().getTime() / (1000 * 60 * 60 * 24));
  }

  getLanguageCode(): string {
    return this.language;
  }

  getBuildId(): string {
    return this.buildId;
  }

  getTrackingId(): string {
    return this.trackingId;
  }
}

export const applicationInfoInstance = new ApplicationInfo();
