export class SystemInfo {
  constructor(
    public hardwareType: number = 0,
    public manufacturer: string = 'ASUS',
    public model: string = 'R540L',
    public majorOSVersion: number = 10,
    public minorOSVersion: number = 8,
    public OSArchitecture: string = 'x64',
    public RAM: number = 4096,
    public processor: string = 'Intel Core i5-5200U',
  ) {}

  getHardwareType(): number {
    return this.hardwareType;
  }

  getManufacturer(): string {
    return this.manufacturer;
  }

  getModel(): string {
    return this.model;
  }
  getMajorOSVersion(): number {
    return this.majorOSVersion;
  }
  getMinorOSVersion(): number {
    return this.minorOSVersion;
  }

  getOSArchitecture(): string {
    return this.OSArchitecture;
  }

  getRAM(): number {
    return this.RAM;
  }

  getProcessor(): string {
    return this.processor;
  }
}

export const systemInfoInstance = new SystemInfo();
