import React from 'react';
import styled from 'styled-components';
import { Button } from '../Buttons';

interface FooterProps {
  footerMessage?: string;
  footerBtnText: string;
  onFooterBtnClick: () => void;
}

const StyledFooter = styled.div`
  display: table;
  width: 100%;
  background-color: ${({ theme }) => theme.colors.lightGrey};

  .footer-text {
    display: table-cell;
    padding-left: 1rem;
    font-size: 0.9375rem;
    vertical-align: middle;

    &__container {
      display: block;
      max-width: 15.625rem;
    }
  }

  .footer-action {
    display: table-cell;
    padding: 1rem;
    text-align: right;
    vertical-align: middle;
  }

  .action-btn {
    display: inline-block;
  }

  .close-btn {
    > img {
      width: 0.9375rem;
    }
  }
`;

export const Footer = ({
  footerMessage,
  footerBtnText,
  onFooterBtnClick,
}: FooterProps): React.ReactElement => {
  return (
    <StyledFooter>
      <div className="footer-text">
        {footerMessage && (
          <span className="footer-text__container">{footerMessage}</span>
        )}
      </div>

      <div className="footer-action">
        <Button text={footerBtnText} onClick={onFooterBtnClick}></Button>
      </div>
    </StyledFooter>
  );
};
