import { darken } from 'polished';
import React from 'react';
import styled from 'styled-components';

import { ThemeColors } from '../../styled.d';

interface StyledButtonProps {
  color?: `${ThemeColors}`;
  size?: string;
}

const StyledButton = styled.button<StyledButtonProps>(
  ({ theme, color }) => `
    position: relative;
    min-width: 6.5625rem;
    padding: 0.625rem 1rem;
    border: 0;
    border-radius: 0.125rem;
    outline: none;
    background: ${color ? theme.colors[color] : theme.colors.primary};
    color: #fff;
    font-size: 0.8125rem;
    font-weight: 700;
    line-height: 1.6;
    text-align: center;
    text-decoration: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    overflow: hidden;

    .btn-inner {
      position: relative;
      z-index: 2;
    }

    &:before,
    &:after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      z-index: 1;
      transition: background-color .05s ease-in-out;
    }

    &:before {
      top: 0;
      bottom: 50%;
      background-color: ${theme.colors.green};
    }

    &:after {
      top: 50%;
      bottom: 0;
      background-color: ${theme.colors.darkGreen};
    }

    &:hover {
      &:before {
        background-color: ${darken(0.025, theme.colors.green)};
      }
      &:after {
        background-color: ${darken(0.025, theme.colors.darkGreen)};
      }
    }

    &:active {
      &:before {
        background-color: ${darken(0.05, theme.colors.green)};
      }
      &:after {
        background-color: ${darken(0.05, theme.colors.darkGreen)};
      }
    }
  `,
);

interface ButtonProps extends StyledButtonProps {
  children?: React.ReactChildren | string;
  icon?: string;
  text?: string;
  onClick?: () => void;
}

export const Button = ({
  children,
  icon,
  text,
  color,
  size,
  onClick,
}: ButtonProps): React.ReactElement => {
  return (
    <StyledButton color={color} size={size} onClick={onClick}>
      <span className="btn-inner">
        {!!icon && <img src={icon} />}
        {!!text && text}
        {children}
      </span>
    </StyledButton>
  );
};
